import { Button, Form, Col, Row } from 'react-bootstrap';
import React, {useState, useCallback, useRef} from 'react'
import Card from './Card'
import PriceTag from './PriceTag'
import * as htmlToImage from 'html-to-image';
import { toPng, toJpeg, toBlob, toPixelData, toSvg } from 'html-to-image';
import Card46 from './4x6/Card46';

const A4 = ({product, props}) => {
    const [show, setShow] = useState(true)

    /* 4，5 x 8 */
    const repeatCardArr1 = [
        {id: 1},{id: 2},{id: 3},{id: 4},{id: 5},{id: 6},{id: 7},{id: 8},{id: 9},{id: 10},{id: 11},{id: 12}
    ]

    /* 4x6 */
    const reapeatCardArr2 = [
        {id: 1},{id: 2},{id: 3},{id: 4},{id: 5},{id: 6},{id: 7},{id: 8},{id: 9},{id: 10},{id: 11},{id: 12},{id: 13},{id: 14},{id: 15},{id: 16},{id: 17},{id: 18},{id: 19},{id: 20}
    ]

    /* 4，5 x 8 */
    const cardsMedium = repeatCardArr1.map((item) =>
    <div key={item.id}>
        <Card show={show}/>
    </div>
    )
    const [card, setCard] = useState('medium')
    /* 4x6 */
    const cardsSmall = reapeatCardArr2.map((item) =>
    <div key={item.id}>
        <Card46 show={show}/>
    </div>
    )

    const changeCard = () => {
        if (card === 'medium') {
            return cardsMedium
        } if (card === 'small') {
            return cardsSmall
        }
    }

    const onCapture = () =>{
        htmlToImage.toJpeg(document.getElementById('prezzo'), { quality: 0.95 })
        .then(function (dataUrl) {
          var link = document.createElement('a');
          link.download = 'my-image-name.jpeg';
          link.href = dataUrl;
          link.click();
        });
       }

    return (
        <div  style={{
            display: 'flex',
        }}
        >
        <div style={{
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'center',
            width: '297mm',
            height: '21cm',
            background: 'white',
            alignItems: 'center'
            
        }}
            id='prezzo'
        >
            <div
                style={{
                    display: 'flex',
                    flexWrap: 'wrap',
                    justifyContent: 'center',
                    width: '241mm',
                    borderRight: '1px solid black',
                }}
            >
                {changeCard()}
            </div>
        </div>
        <div>
        {show && 
        <div style={{
            display: 'flex',
            flexDirection: 'column'
        }}>
        <Button onClick={() => setShow(false)} style={{
            margin: '30px'}}>Done</Button>

        <Button onClick={() => setCard('medium')}
        variant="outline-primary"
        style={{
            marginLeft: '30px'}}
        >4.5cm X 8cm (Medium)</Button>
        <Button
        variant="outline-success"
        onClick={() => setCard('small')} style={{
            margin: '30px'}}>4cm x 6cm (Small)</Button>

        </ div>
            }
        {!show && <Button onClick={() => onCapture()} style={{
            margin: '30px'}}>Download</Button>} 
        </div>  
        </ div>
    )
}

export default A4
